import { login } from '../../util/login.js';
export default {
	data() {
		return {
		};
	},
	methods:{
		closeModal(){
			this.$emit('cancelEmit')
		},
		getUserInfo(e) {
			let _this = this
			if(e.detail.errMsg == 'getUserInfo:fail auth deny')return
			let par = {
				vm:_this,
				userInfo:e.detail.userInfo
			}
			login().then(()=>{
				_this.$emit('cancelEmit')
				_this.$store.dispatch('findUserById',par)
			})
		}
	}
}
